"use strict";

import {Component, ViewChild, OnInit, ViewEncapsulation} from '@angular/core';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Ville} from "../metier/ville.metier";
import {FormGroup, FormBuilder} from "@angular/forms";
import {BaseComponent} from "../../../core/component/base-component";
import {VilleService} from "../service/ville.service";
import 'rxjs/Rx' ;
import 'rxjs/add/operator/toPromise';
import {saveAs} from 'file-saver/FileSaver';

@Component({
  selector: 'ms-ville-list',
  templateUrl: './ville.list.component.html',
  styleUrls: ['./ville.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class VilleListComponent extends BaseComponent implements OnInit {

  public villeForm: FormGroup;
  private ville: Ville;
  private villeList: Array<Ville> = [];
  private file: Blob;

  constructor(private fb: FormBuilder,
              private villeService: VilleService,
              private pageTitleService: PageTitleService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES VILLES");
    this.loadVilleList();

  }

  createForm() {
    this.villeForm = this.fb.group({
      libelle: '',
    });
  }


  findVilleList(villeForm: any) {

    this.ville = new Ville(villeForm);
    this.villeService.findVilleListByCriteria(this.ville)
      .subscribe(villeList => {
        this.villeList = villeList;
        this.getPdfFile();

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private loadVilleList() {

    this.villeService.getVilleList()
      .subscribe(villeList => {
        this.villeList = villeList;
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private getPdfFile() {
    this.villeService.downloadPdf()
      .subscribe(file => {
          this.file = file;
          this.downloadFile(this.file);
        }, error => this.showError(error.status, error.json().message)
      );
  }

}






