import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {VilleAddComponent}  from './ville.add/ville.add.component';
import {VilleRoutes} from './ville.routing';
import {VilleService} from "./service/ville.service";
import {VilleListComponent} from "./ville.list/ville.list.component";
import {VilleEditComponent} from "./ville.edit/ville.edit.component";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(VilleRoutes)
  ],
  declarations: [
    VilleAddComponent,
    VilleListComponent,
    VilleEditComponent,
  ],
  providers:[
    VilleService,
  ],
})

export class VilleModule {
}



