import {Routes} from '@angular/router';

import {VilleAddComponent}  from './ville.add/ville.add.component';
import {VilleListComponent} from "./ville.list/ville.list.component";
import {VilleEditComponent} from "./ville.edit/ville.edit.component";

export const VilleRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: VilleAddComponent},
    {path: 'list', component: VilleListComponent},
    {path: 'edit/:id', component: VilleEditComponent},

  ]
}];



