import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Ville} from "../metier/ville.metier";
import {VilleService} from "../service/ville.service";
import {BaseComponent} from "../../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";

@Component({
  selector: 'ms-ville-edit',
  templateUrl: './ville.edit.component.html',
  styleUrls: ['./ville.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class VilleEditComponent extends BaseComponent implements OnInit {
  private ville:Ville;
  public villeForm:FormGroup;


  constructor(private fb:FormBuilder,
              private route:ActivatedRoute,
              private pageTitleService:PageTitleService,
              private villeService:VilleService) {
    super();
  }

  ngOnInit() {
    this.loadVille();
    this.pageTitleService.setTitle("MODIFIER UNE VILLE");
    this.villeForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],

    });
  }


  updateVille(villeForm:any) {
    this.ville = new Ville(villeForm);
    this.villeService.saveVille(this.ville)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/ville/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadVille() {
    this.route.params.switchMap((params:Params) => this.villeService.getVille(+params['id']))
      .subscribe(ville => {
          this.ville = ville;
          this.villeForm.patchValue(this.ville);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteVille(villeForm:any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.ville = new Ville(villeForm);
    this.villeService.deleteVille(this.ville)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/ville/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }


}






